<template>
  <section class="page-container" >
    <div class="disease-wrap">
      <span class="left">选择病种</span>
      <a class="disease-select" @click="showPopup">{{selectDisease}}</a>
    </div>
    <div class="edit-container">
      <p>病情描述</p>
      <textarea placeholder="请详细描述您的症状，疾病和身体状况，便于医生能准确为您作出判断">
      </textarea>
    </div>
    <popup
      v-model="popupVisible"
    >
      <div class="disease-list">
        <div
          class="disease-item"
          v-for="item in diseaseList"
          :class="{active: item === selectDisease}"
          @click="diseaseClick(item)"
        >
          <div>{{item}}</div>
        </div>
      </div>
    </popup>
  </section>
</template>

<script type="text/Babel">
  export default {
    data() {
      return {
        popupVisible: false,
        diseaseList: ['心脑血管', '心内病', '神经外科', '神经内科', '妇科', '便秘', '其他儿科问题'],
        selectDisease: '请选择疾病类型',
      };
    },
    mounted() {

    },
    methods: {
      showPopup() {
        this.popupVisible = true;
      },
      diseaseClick(item) {
        this.selectDisease = item;
        this.popupVisible = false;
      },
    },
  };
</script>

<style lang="scss" rel="stylesheet/scss" type="text/scss" scoped>
  @import "../assets/style/variables";
  @import "../assets/style/mixin";

  .disease-wrap {
    background-color: $whiteColor;
    padding: 0 .8rem;
    height: 3rem;
    line-height: 3rem;
    text-align: left;
    font-size: $fontPoint7rem;

    .disease-select {
      position: relative;
      padding-right: 1rem;
      float: right;
      color: $textGrayColor;

      &:active {
        opacity: .6;
      }

      &:after {
        box-sizing: content-box;
        content: "";
        border: 2px solid $textDarkGrayColor;
        border-left: 0;
        border-top: 0;
        height: .5rem;
        width: .5rem;
        right: 0px;
        top: 1rem;
        position: absolute;
        transform-origin: center;
        transform: rotate(45deg) scaleY(1);
      }
    }
  }

  .edit-container {
    margin-top: .5rem;
    padding: .8rem;
    background-color: $whiteColor;
    text-align: left;
    font-size: $fontPoint6rem;

    textarea {
      width: 100%;
      height: 10rem;
      margin-top: .3rem;
      line-height: 1rem;
      font-size: $fontPoint6rem;
      border: 0;
      outline: none;
      resize: none;
    }
  }

  .disease-list {
    width: 100%;
    border-top: 1px solid $borderColor;

    .disease-item {
      position: relative;
      padding: 0 .8rem;
      background-color: $whiteColor;
      font-size: $fontPoint7rem;

      > div {
        position: relative;
        line-height: 2rem;
        text-align: left;
        border-bottom: 1px solid $borderColor;
      }

      &:active {
        background-color: $textGrayColor;
        color: $blueColor;
      }

      &.active {
        > div {
          color: $blueColor;

          &:after {
            box-sizing: content-box;
            content: "";
            border: 2px solid $blueColor;
            border-left: 0;
            border-top: 0;
            height: 12px;
            width: 5px;
            position: absolute;
            top: 47%;
            right: 10px;
            transition: transform .15s cubic-bezier(.71,-.46,.88,.6) .05s;
            transform-origin: center;
            transform: rotate(45deg) scaleY(1) translateY(-50%);;
          }
        }
      }
    }
  }
</style>



